<template>
  <button @click="onClick" class="pill">
    {{ text }}
  </button>
</template>
<script>
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    text: String,
    file: String,
  },
  setup(props, context) {
    const onClick = () => {
      context.emit("play", props.file);
    };

    return {
      onClick,
    };
  },
});
</script>
<style scoped>
.pill {
  width: 30vw;
  height: 100px;
  border-radius: 6px;
  margin: auto;
  text-align: center;
  position: relative;
  background: rgba(0, 0, 0, 0.755);
  color: rgb(235, 235, 235);
  font-weight: 600;
  font-size: 16px;
}

</style>
