<template>
  <div class="SoundBoard">
    <SoundPill
      text="Sound 1"
      file="1.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 2"
      file="2.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 3"
      file="3.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 4"
      file="4.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 5"
      file="5.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 6"
      file="6.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 7"
      file="7.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 8"
      file="8.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 9"
      file="9.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 10"
      file="10.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 11"
      file="11.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 12"
      file="12.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 13"
      file="13.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 14"
      file="14.mp3"
      @play="onPlay"
      @click="handleActive"
    />
    <SoundPill
      text="Sound 15"
      file="15.mp3"
      @play="onPlay"
      @click="handleActive"
    />
  </div>
</template>
<script>
import { defineComponent } from "vue";
import SoundPill from "./SoundPill.vue";

export default defineComponent({
  components: {
    SoundPill,
  },
  setup() {
    var audio = new Audio();

    const removeActive = () => {
      let pills = document.getElementsByClassName("pill");
      for (let i = 0; i < pills.length; i++) {
        pills[i].classList.remove("active");
      }
    };

    const onPlay = (fileName) => {
      audio.pause();
      audio = new Audio("sofw/assets/audio/" + fileName);
      audio.play();
      audio.onended = () => {
        removeActive();
      };
    };

    const handleActive = (evt) => {
      let pills = document.getElementsByClassName("pill");
      for (let i = 0; i < pills.length; i++) {
        pills[i].classList.remove("active");
      }
      evt.target.classList.add("active");
    };

    return {
      onPlay,
      handleActive,
    };
  },
});
</script>
<style scoped>
.SoundBoard {
  padding-top: 16px;
  padding-bottom: 16px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  background: none;
}

.active {
  color: #4088f3;
  box-shadow: 0 0 5px #4088f3;
}
</style>
